@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
    font-family: 'Open Sans', sans-serif;
}
.remove-btn {
    margin-right: 0.5rem;
    text-align: center;
}

.fade-enter {
    opacity: 0.01;
}

.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity 1000ms ease-in;
    transition: opacity 1000ms ease-in;
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    opacity: 0.01;
    -webkit-transition: opacity 1000ms ease-in;
    transition: opacity 1000ms ease-in;
}
body {
    font-family: 'Open Sans', sans-serif;
}
